import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import WelcomePage from '@/components/WelcomePage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'WelcomePage',
      component: WelcomePage
    }
  ]
})



